<template>
  <div id="app">
    <div v-if="hasFilterData" class="row mb-4 filter">
      <div v-for="filter in filterData" :key="filter.name" class="col">
        <label
          v-if="filter.type !== 'checkbox'"
          :for="filter.name"
          class="form-label"
        >
          {{ filter.label }}
        </label>
        <template v-if="filter.type === 'password'">
          <input
            :id="filter.name"
            v-model.trim="filter.value"
            class="form-control input-password"
            :type="filter.type"
            :name="filter.name"
            :title="filter.label"
            :required="filter.required"
            :disabled="!filter.enabled"
          />
          <a href="javascript:void(0)" class="show-pass"
            ><i class="fa fa-eye-slash"></i
          ></a>
        </template>
        <template v-if="filter.type === 'number'">
          <input
            :id="filter.name"
            v-model.trim="filter.value"
            class="form-control"
            :type="filter.type"
            :name="filter.name"
            :title="filter.label"
            :min="filter.min"
            :max="filter.max"
            :step="filter.step"
            :required="filter.required"
            :disabled="!filter.enabled"
          />
        </template>
        <template v-if="filter.type === 'date'">
          <datetime
            v-model.trim="filter.value"
            format="dd MMM yyyy"
            input-class="form-control"
            :type="filter.type"
            :name="filter.name"
            :title="filter.label"
            :input-id="filter.name"
            autocomplete="off"
            :auto="true"
            :required="filter.required"
            :disabled="!filter.enabled"
          ></datetime>
        </template>
        <template v-if="filter.type === 'datetime'">
          <datetime
            v-model.trim="filter.value"
            format="dd MMM yyyy HH:mm a"
            input-class="form-control"
            :type="filter.type"
            :name="filter.name"
            :title="filter.label"
            :input-id="filter.name"
            autocomplete="off"
            :auto="true"
            :required="filter.required"
            :disabled="!filter.enabled"
          ></datetime>
        </template>
        <template v-if="filter.type === 'paragraph'">
          <input
            :id="filter.name"
            v-model.trim="filter.value"
            class="form-control"
            :type="filter.type"
            :name="filter.name"
            :title="filter.label"
            readOnly="true"
            :required="filter.required"
            :disabled="!filter.enabled"
          />
        </template>
        <template v-if="filter.type === 'text'">
          <input
            :id="filter.name"
            v-model="filter.value"
            class="form-control"
            :type="filter.type"
            :name="filter.name"
            :title="filter.label"
            :required="filter.required"
            :disabled="!filter.enabled"
          />
        </template>
        <template v-if="filter.type === 'textarea'">
          <textarea
            :id="filter.name"
            v-model="filter.value"
            class="form-control"
            :type="filter.type"
            :name="filter.name"
            :title="filter.label"
            :rows="filter.rows"
            :cols="filter.cols"
            :required="filter.required"
            :disabled="!filter.enabled"
          ></textarea>
        </template>
        <template v-if="filter.type === 'checkbox'">
          <b-form-checkbox
            :id="filter.name"
            v-model="filter.value"
            :name="filter.name"
            :disabled="!filter.enabled"
          >
            {{ filter.label }}
          </b-form-checkbox>
        </template>
        <template v-if="filter.type === 'select'">
          <multiselect
            v-model.trim="filter.selectedValue"
            :options="filter.options"
            :searchable="true"
            :show-labels="true"
            label="label"
            track-by="label"
            :required="filter.required"
            :disabled="!filter.enabled"
          ></multiselect>
        </template>
        <template v-if="filter.type === 'selectsearch'">
          <multiselect
            v-model.trim="filter.value"
            :options="filter.options"
            label="label"
            track-by="label"
            :preserve-search="true"
            :reset-after="false"
            group-values="groupOptions"
            group-label="label"
            :required="filter.required"
            :disabled="!filter.enabled"
          ></multiselect>
        </template>
        <template v-if="filter.type === 'multiselect'">
          <multiselect
            v-model="filter.selectedValues"
            :options="filter.options"
            :multiple="true"
            :close-on-select="false"
            :preserve-search="true"
            label="label"
            track-by="label"
            :reset-after="false"
            group-values="groupOptions"
            group-label="label"
            :group-select="true"
            :hide-selected="true"
            :required="filter.required"
            :disabled="!filter.enabled"
          ></multiselect>
        </template>
      </div>
      <div class="col filter-col-btn">
        <div class="filter-button filter-submit">
          <label class="form-label"> &nbsp; </label>
          <button
            class="form-control btn btn-primary"
            type="submit"
            @click.prevent="submitForm"
          >
            Филтрирай
          </button>
        </div>

        <div v-if="hasActiveFilters" class="filter-button filter-reset">
          <div class="mb-3">
            <label class="form-label"> &nbsp; </label>
            <button
              class="form-control btn btn-danger"
              type="button"
              @click.prevent="resetFilters"
            >
              Изчисти
            </button>
          </div>
        </div>
      </div>
    </div>

    <div class="row mb-4 sort">
      <span class="col-1 mr-4">Показване: </span>
      <b-select
        v-model="perPage"
        class="col-2 px-3"
        :options="totalPagesOptions"
        @change="setPerPage"
      ></b-select>
    </div>

    <div v-if="hasTableData">
      <b-table
        :items="items"
        :fields="fields"
        :sort-by.sync="sortBy"
        :sort-desc.sync="sortDesc"
        sort-icon-left
        :current-page="currentPage"
        no-sort-reset
        responsive="sm"
        @sort-changed="setSorting"
      >
        <!-- eslint-disable vue/no-v-html -->
        <template #cell()="data">
          <div v-html="data.value"></div>
        </template>
        <!-- eslint-enable vue/no-v-html -->
        <template #cell(Actions)="data">
          <a
            v-for="action in data.item.Actions"
            :key="action.key"
            :class="btnClass(action.key)"
            :href="action.url"
            style="margin: 0 1px"
            v-html="btnIconClass(action.key)"
          >
          </a>
        </template>
      </b-table>
      <b-pagination-nav
        v-model="currentPage"
        :number-of-pages="totalPages"
        base-url="#"
        prev-text="Предишна"
        next-text="Следваща"
        size="sm"
        class="my-0"
        @change="setPage"
      ></b-pagination-nav>
    </div>
  </div>
</template>

<script>
const config = {
  initCurrentPage: 1,
  initPerPage: 5,
}

export default {
  name: 'App',
  data() {
    return {
      initFilterData: {},
      tableData: {},
      filterData: {},
      totalPages: null,
      perPage: config.initPerPage,
      currentPage: config.initCurrentPage,
      rows: null,
      totalPagesOptions: [
        { value: 5, text: '5' },
        { value: 10, text: '10' },
        { value: 15, text: '15' },
        { value: 20, text: '20' },
      ],
      requestedFilters: false,
    }
  },
  computed: {
    hasTableData() {
      return Object.keys(this.tableData).length !== 0
    },
    hasFilterData() {
      return this.tableData.length !== 0
    },
    hasActiveFilters() {
      return !this.$lodash.isEqual(this.filterData, this.initFilterData)
    },
    isPerPageInit() {
      return this.perPage == config.initPerPage
    },
    isCurrentPageInit() {
      return this.currentPage == config.initCurrentPage
    },
    fields() {
      if (this.tableData.length !== 0) {
        return this.tableData.columns
      }
      return []
    },
    items() {
      if (this.tableData.length !== 0) {
        const items = this.tableData.rows.map((row) => ({
          objectId: row.objectId,
          ...(() => {
            const cells = row.cells.map((cell) =>
              cell.key !== 'actions'
                ? {
                    [cell.key]: cell.text,
                    [cell.link]: cell.href,
                  }
                : { Actions: cell.actions }
            )
            return Object.assign({}, ...cells)
          })(),
        }))
        return items
      }
      return []
    },
    query() {
      return this.$lodash.pickBy({
        currentPage: Number(this.currentPage),
        perPage: Number(this.perPage),
      })
    },
  },
  watch: {
    '$route.query': {
      handler(query, prevQuery) {
        this.currentPage = Number(query.currentPage || config.initCurrentPage)
        this.perPage = Number(query.perPage || config.initPerPage)

        if (!this.$lodash.isEqual(query, prevQuery)) {
          this.submitForm()
        }
      },
      deep: true,
    },
  },
  mounted() {
    this.getData()
  },
  methods: {
    async getData() {
      try {
        const { data } = await this.axios.get(window.jsonData.jsonUrlFilter)
        this.filterData = data
        this.initFilterData = this.$lodash.cloneDeep(this.filterData)

        console.log('filterData', this.filterData)

        if (!this.$lodash.isEmpty(this.$route.query)) {
          this.currentPage = Number(
            this.$route.query.currentPage || config.initCurrentPage
          )
          this.perPage = Number(this.$route.query.perPage || config.initPerPage)

          this.submitForm()
        } else {
          const apiUrl = window.jsonData

          const { data: tableData } = await this.axios.get(apiUrl.jsonUrlTable)

          this.tableData = tableData
          this.totalPages = tableData.totalPages
          this.currentPage = Number(
            this.$route.query.currentPage || tableData.currentPage
          )
          this.perPage = Number(
            this.$route.query.perPage || tableData.numberOfRowsPerPage
          )
          this.rows = tableData.rows.length
        }
      } catch (e) {
        console.error('Error loading filter data and table data', '\n', e)
      }
    },
    async submitForm() {
      try {
        const additionalPayload = {
          currentPage: Number(this.currentPage),
          numberOfRowsPerPage: Number(this.perPage),
        }

        const payload = this.$lodash
          .cloneDeep(this.filterData)
          .concat(additionalPayload)

        console.log('submitted with:', payload)

        if (!this.$lodash.isEmpty(this.filterData)) {
          this.requestedFilters = true
        }

        const apiUrl = window.jsonData

        const { data: newTableData } = await this.axios.post(
          apiUrl.jsonUrlTable,
          payload
        )
        console.log('returned:', newTableData)

        this.tableData = newTableData
        this.totalPages = newTableData.totalPages
        this.currentPage = newTableData.currentPage
        this.perPage = newTableData.numberOfRowsPerPage
        this.rows = newTableData.rows.length
      } catch (e) {
        console.error('Error submitting filter:', '\n', e)
      }
    },
    btnClass(type) {
      switch (type) {
        case 'view':
          return 'btn btn-success'
        case 'history':
          return 'btn btn-info'
        case 'edit':
          return 'btn btn-warning '
        case 'delete':
          return 'btn btn-danger'
        case 'rights':
          return 'btn btn-rights'
      }
    },
    btnIconClass(type) {
      switch (type) {
        case 'view':
          return 'mdi mdi-eye-outline'
        case 'history':
          return 'mdi mdi-progress-clock'
        case 'edit':
          return 'Преглед'
        case 'delete':
          return 'dripicons-trash'
        case 'rights':
          return 'mdi mdi-key-outline'
      }
    },
    filterColClass(col) {
      return `col-md-${col}`
    },
    setQuery() {
      this.$router.push({ query: this.query }).catch(() => {})
    },
    setPage(page) {
      this.currentPage = Number(page)
      this.setQuery()
    },
    setPerPage(value) {
      this.perPage = Number(value)
      this.setQuery()
    },
    resetFilters() {
      if (this.requestedFilters === true) {
        this.submitForm()
      }
      this.filterData = this.$lodash.cloneDeep(this.initFilterData)
      this.requestedFilters = false
    },
  },
}
</script>
